import { useState } from "react"
import { useAtom } from "jotai"
import { generatePath, useNavigate } from "react-router-dom"
import { Watchlist } from "@rp/common/api-types"
import { formatTime } from "@rp/common/utils"
import { atomBriefWatchlistFilter } from "@rp/react/common/brief"
import { PrimaryCheckedLabel, RPTooltip } from "@rp/react/web/common/base-components"
import { HubTableRow } from "@rp/react/web/common/composite-components"
import { Box, IconButton, Typography } from "@rp/react/web/mui/core"
import { EditNote } from "@rp/react/web/mui/icons"
import { BRIEF_ROUTE_EDIT } from "@rp/react/web/router"
import { BriefWatchlistItemRadioButton } from "./BriefWatchlistItemRadioButton"

type BriefUserWatchlistItemProps = {
  item: Watchlist
  columnsWidth: Array<string>
  briefId?: string | undefined
  watchlistId: string
  setWatchlistId: (watchlistId: string) => void
  setName: (name: string) => void
}

const BriefUserWatchlistItem = ({
  item,
  columnsWidth,
  briefId,
  setWatchlistId,
  watchlistId,
  setName,
}: BriefUserWatchlistItemProps) => {
  const navigate = useNavigate()
  const [openMenu] = useState(false)
  const [filterTerm] = useAtom(atomBriefWatchlistFilter)
  const alreadyScheduled = !!briefId
  return (
    <HubTableRow
      sx={{ gap: 1, px: 0 }}
      componentProps={{ filterTerm }}
      selected={openMenu}
      showCompanyIcon={item.shared?.company.permission !== "undefined"}
      columnsWidth={columnsWidth}
      highlightColumnIndices={[1]}
      columns={[
        {
          node: (
            <BriefWatchlistItemRadioButton
              disabled={alreadyScheduled}
              watchlist={item}
              watchlistId={watchlistId}
              setWatchlistId={setWatchlistId}
              setName={setName}
            />
          ),
        },
        { text: item.name },
        {
          node: (
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <Typography flexGrow={1}>{formatTime(item.lastUpdated)}</Typography>
              {alreadyScheduled && (
                <>
                  <PrimaryCheckedLabel label="Already scheduled" />
                  <RPTooltip title="Edit Brief">
                    <IconButton
                      size="small"
                      onClick={() => navigate(generatePath(BRIEF_ROUTE_EDIT, { briefId }))}
                    >
                      <EditNote />
                    </IconButton>
                  </RPTooltip>
                </>
              )}
            </Box>
          ),
        },
      ]}
    />
  )
}
export { BriefUserWatchlistItem }
